<!--
        房屋详情页面
-->
<template>
    <div>
        <el-drawer
                title="您已经浏览本页面一分钟啦,不如看看以下房源推荐?"
                :visible.sync="ShowDrawer"
                :with-header="true">
            <span>推荐不动了哈哈哈哈哈哈操!</span>
        </el-drawer>

        <!--
        顶部 Navbar
        -->
        <el-menu
                class="el-container headerNav"
                style="height: 70px;z-index: 1;"
                mode="horizontal"
                router
                active-text-color="#409EFF"
                ref="headerNav">
            <!--logo 的 svg-->
            <el-menu-item class="el-col-3 el-col-push-0" index="0" style="text-decoration: none" route="/">
                <div style="height: 100%;width: 100%;text-decoration: none">
                    <svg width="121" height="52" xmlns="http://www.w3.org/2000/svg"
                         xmlns:xlink="http://www.w3.org/1999/xlink">
                        <g>
                            <title>background</title>
                            <rect fill="#fff" id="canvas_background" height="54" width="123" y="-1" x="-1" />
                            <g display="none" overflow="visible" id="canvasGrid">
                                <rect fill="url(#gridpattern)" stroke-width="0" y="0" x="0" height="100%"
                                      width="100%" />
                            </g>
                        </g>
                        <g>
                            <title>Layer 1</title>
                            <image xlink:href="data:image/png;base64,"
                                   id="svg_1" height="63.23905" width="143.00001" y="-5.0996" x="-9.00001" />
                        </g>
                    </svg>
                </div>
            </el-menu-item>
            <el-menu-item v-if="!isLogin" class="el-col-1 el-col-push-20 headerItems" index="1"
                          route="/login">登录 | 注册
            </el-menu-item>

            <el-menu-item v-if="isLogin" class="el-col-1 el-col-push-19 headerItems" index="2"
                          @click="$router.push({path: '/user' + '/' + userData.userPhone + '/' + userData.userName})">
                {{userData.userName}}
            </el-menu-item>
            <el-menu-item v-if="isLogin" class="el-col-1 el-col-push-19 headerItems" index="3"
                          route=""
                          @click="logout">
                退出
            </el-menu-item>
        </el-menu>
        <div style="display:flex;flex-direction: column;">
            <el-carousel :interval="4000" type="card" height="50vh">
                <el-carousel-item v-for="item in houseImages" :key="item">
                    <el-image class="hvr-grow"
                              :src="item"
                              fit="fill" />
                </el-carousel-item>
            </el-carousel>
            <div style="display: flex;flex-direction: row;">
                <!--
                房屋信息介绍
                -->
                <div style="width: 40vw;margin-left: 10vw;margin-top: 1vh;position:absolute;">
                    <el-collapse v-model="activeNames" @change="handleChange">
                        <el-collapse-item title="基本信息 Basic Information" name="1">
                            <div>
                                <p v-show="false">
                                    usId：{{houseDetails.usId}}
                                    id：{{houseDetails.house.id}}
                                    pictureUrl：{{houseDetails.house.pictureUrl}}
                                </p>
                            </div>

                            <div style="text-align: left;display: flex;flex-direction: column;font-family: ShouShuTi,sans-serif;color: #528489;font-size: 18px">
                                <span style="font-size: 35px;font-family: DengXian_Reg,sans-serif;color: #2c3e50">{{houseDetails.house.name}}</span>
                                <br>
                                <p>租赁类型：{{houseDetails.house.rentalType}}</p>
                                <p>房屋朝向：{{houseDetails.house.orientation}}</p>
                                <p>面积：{{houseDetails.house.area}}</p>
                                <p>价格：{{houseDetails.house.price}}</p>
                                <p>楼层：{{houseDetails.house.floor}}</p>
                            </div>
                        </el-collapse-item>
                        <el-collapse-item title="详细信息 Details" name="2">
                            <div style="text-align: left;display: flex;flex-direction: column;color: #528489;font-family: ShouShuTi,sans-serif;font-size: 18px">
                                <p> 电视：{{houseDetails.houseExt.television==='否'?'无':'有'}} </p>
                                <p> 冰箱：{{houseDetails.houseExt.fridge==='否'?'无':'有'}} </p>
                                <p> 洗衣机：{{houseDetails.houseExt.washingMachine==='否'?'无':'有'}} </p>
                                <p> 空调：{{houseDetails.houseExt.airConditioning==='否'?'无':'有'}} </p>
                                <p> 取暖器：{{houseDetails.houseExt.heater==='否'?'无':'有'}} </p>
                                <p> 床：{{houseDetails.houseExt.bed==='否'?'无':'有'}} </p>
                                <p> 临近地铁线路：{{houseDetails.houseExt.subwayLine}} </p>
                                <p> 临近地铁站：{{houseDetails.houseExt.subwayStation}} </p>
                            </div>
                        </el-collapse-item>
                        <el-collapse-item title="更多介绍 More Details" name="3">
                            <div style="text-align: left;display: flex;flex-direction: column;color: #528489;font-family: ShouShuTi,sans-serif;font-size: 18px">
                            {{houseDetails.houseExt.description}}
                            </div>
                        </el-collapse-item>
                    </el-collapse>
                </div>
                <el-card style="width: 20vw;margin-left: 60vw;margin-top: 1vh;position:fixed;bottom: 10vh">
                    <div style="display:flex;flex-direction: column;width: 100%;height: 100%">
                        <div style="width: 100%;">
                            <span style="line-height: 100%;font-family: ShouShuTi,serif;color: #528489;font-size: 28px">￥{{houseDetails.house.price}}</span>
                        </div>
                        <!--                    <div style="width: 100%;">-->
                        <!--                        <span style="float:left;line-height: 100%">日期</span>-->
                        <!--                    </div>-->
                        <!--                    <div style="margin-top: 2%">-->
                        <!--                        <el-date-picker type="date" placeholder="选择日期" v-model="rentInfo.beginDate" style=" width: 40%;float: left"></el-date-picker>-->
                        <!--                        <i class="el-icon-right" style="float: left;width: 19%;margin-top: 2%"/>-->
                        <!--                        <el-date-picker type="date" placeholder="选择日期" v-model="rentInfo.endDate" style="width: 40%;float: left"></el-date-picker>-->
                        <!--                    </div>-->
                        <div style="margin-bottom: 2%;margin-top: 5%">
                            <div style="width: 100%;">
                                <span style="float:left;line-height: 100%;color: #0f9e5e">预订月数</span>
                            </div>
                            <el-input-number style="margin-top: 5%;margin-bottom: 5%;" v-model="rentInfo.num" :min="1"
                                             :max="5" label="入住人数" class="el-col-24"></el-input-number>

                            <div>
                                <div style="color: #8492a6;font-size: small">
                                    总价：{{parseInt(houseDetails.house.price.split('元')[0]) * rentInfo.num}}元
                                </div>
                            </div>
                        </div>
                        <el-button><span style="color: #0f9e5e;font-weight: bold">立即预订</span></el-button>
                    </div>
                </el-card>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'HouseDetail',
  data () {
    return {
      ShowDrawer: false,
      beginTime: '',
      endTime: '',
      sentTime: false,
      cityPyName: '',
      houseId: '',
      isLogin: false,
      userData: [],
      houseDetails: {
        house: {
          pictureUrl: ''
        }
      },
      activeNames: ['1'],
      houseImages: ['https://z1.muscache.cn/im/pictures/d9004ccb-3175-44a2-9010-66d156c5220a.jpg?aki_policy=large',
        'https://z1.muscache.cn/im/pictures/095eead2-042b-464e-9cea-2a2ca30ac236.jpg?aki_policy=xx_large',
        'https://z1.muscache.cn/im/pictures/d62f6b81-2f2d-4cb0-81c0-b9d996623a40.jpg?aki_policy=large',
        'https://z1.muscache.cn/im/pictures/fa48d953-61cc-42cc-8a69-f2bd041d1d16.jpg?aki_policy=large'],
      rentInfo: {
        num: 1,
        beginDate: '',
        endDate: ''
      },
      HouseData: [],
      HouseDataLength: 3
    }
  },
  watch: {},
  methods: {
    /*
    * 退出登录：清除本地 cookie；调用后台接口
    */
    logout () {
      const that = this
      that.$http.get('/i/logout', {
        params: {
          Cookie: 'token=' + that.$cookie.get('token')
        }
      })
        .then(function (response) {
          that.$notify.success({
            title: '注销成功',
            message: '您的账号已退出登录'
          })
          // 删除 token
          that.$cookie.delete('token')
          // 删除 userData
          that.$cookie.delete('userData')
          that.userData = ''
          that.isLogin = false
        })
        .catch(function (error) {
          that.$notify.success({
            title: '注销失败',
            message: error
          })
        })
        .then(function () {
          that.$router.push('/refreshIndex')
        })
    },
    /*
      * 用户点击房屋描述选项卡
      */
    handleChange (val) {
      console.log(val)
    }
  },
  mounted () {
    const that = this
    window.setInterval(() => {
      setTimeout(function () {
        that.ShowDrawer = true
      }, 0)
    }, 60000)
  },
  created () {
    const that = this
    that.beginTime = new Date().getTime()
    that.$notify.info({
      title: '进入时间',
      message: that.beginTime
    })
    that.cityPyName = this.$route.path.split('/')[1]
    that.houseId = this.$route.path.split('/')[3]
    /*
      * 获取用户信息
      */
    setTimeout(function () {
      // 如果存在且未过期
      if (that.$cookie.get('token') !== null && that.$cookie.get('token') !== undefined && that.$cookie.get('token') !== '') {
        const loading = that.$loading({
          lock: true,
          text: '尝试自动登录中',
          background: 'rgba(245,245,245,0.7)'
        })
        /*
          * **********************************************************************
          * 用 cookie 请求用户信息
          */
        that.$http.get('/i', {
          params: {
            Cookie: that.$cookie.get('token')
          }
        })
          .then(function (response) {
            if (response) {
              // 将请求到的数据写到用户信息中
              that.userData = response.data.data
              // 将用户信息保存到 cookie,一小时过期
              that.$cookie.set('userData', response.data.data, {expires: '1h'})
              // 设置为已登录
              that.isLogin = true
            }
          })
          .catch(function (error) {
            that.$notify.error({
              title: '登陆失败',
              message: error
            })
          })
          .then(function () {
            loading.close()
          })
        /************************************************************************/
      } else {
        that.$router.push('/login')
      }
    }, 500)

    that.$http.get('/zufang/' + that.cityPyName + '/f/' + that.houseId, {
      params: {},
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(function (response) {
        if (response.data.errorCode === 200) {
          console.log(response.data.data)
          that.houseDetails = response.data.data
          if (response.data.data.usId !== undefined && response.data.data.usId !== '') {
            that.sentTime = true
          }
        }
      })
      .catch(function (error) {
        console.log(error)
      })
      .then(function () {
      })
  },
  /*
    * 用户退出页面之前
    */
  beforeDestroy () {
    const that = this
    that.endTime = new Date().getTime()
    const stayTime = that.endTime - that.beginTime
    this.$notify.info({
      title: '停留时间',
      message: (stayTime / 1000) + '秒'
    })
    that.$http.post('/page_on_time', {
      usId: that.houseDetails.usId,
      startTime: that.beginTime,
      endTime: that.endTime,
      pageOnTime: stayTime
    }, {headers: {'Content-Type': 'application/json'}})
      .then(function (response) {
        console.log(response)
      })
      .catch(function (error) {
        alert(error)
      })
  }
}
</script>

<style scoped>
    body {
        overflow: scroll;
    }
</style>



